.ourCollector_container{
    .commonContent{
        width: 90%;
        margin-top: 30px;
        p {
            color: $primary-color;
            font-size: 1.6rem;
            line-height: 30px;
            font-family: $font-regular;
            margin-top: 0;
        }
    }
    .rightSliderNav{
        margin-top: 30px;
        justify-content: flex-end;
    }

    .ourCollector_slider_container{
        margin-top: 30px;
        .swiper-wrapper{         
            .item{
                border: 7px solid $color-white;
                transition: 0.5s ease all;
                &:hover, &.swiper-slide-next{
                    border: 7px solid $primary-color;
                    .collectorProfile{
                        opacity:1;
                    }
                }
                img {
                    width: 100%;
                }
            }
            .imgSec{
                position: relative;
                .playBtn{
                    display: block;
                    width: 60px;
                    height:60px;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            .collectorProfile{
                display: flex;
                justify-content: space-between;
                align-items:center;
                padding: 10px 18px;
                transition: 0.5s ease all;
                opacity: 0;
                .profile{
                    display: flex;
                    align-items:center;
                    .nameDetail{
                        margin-left:15px;
                    }
                    h4{
                        color: $primary-color-yellow;
                        font-weight: normal;
                        font-family: $font-default;
                        font-size: 2rem;
                    }
                    span{
                        display: block;
                        color: $primary-color;                                                
                        font-size: 1.4rem;
                        margin-top: 5px;
                    }
                    .profilePic{
                        width: 70px;
                    }
                }
                .quoteImg{
                    background:url('/images/quote.svg') no-repeat 0 0;
                    width:55px;
                    height: 40px;
                    background-size: contain;
                }
            }
        }
    }
}

.commonPopup{
    position: fixed;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    z-index: 3000;
    width:70%;
    height:60vh;
    padding:20px;
    background:#fff;
    display: none;
    .closeBtn{
        position: absolute;
        right:-15px;
        top:-15px;
        background-color: #FFD018;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
    }
    .content{
        height: 100%;
    }
    iframe{
        width:100%;
        height: 100%;
    }
}