.recent_slider_container{
    margin-top: 50px;
    .swiper-wrapper{
        .item{
            position: relative;
            img{
                width: 100%;                                
                border-radius: 10px;
            }

            .recent_content{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                padding: 30px;
                background-color: $color-light-grey;
                border-radius: 10px;
                transition: 0.5s;
                h5{
                    font-size: 3.5rem;
                    font-weight: lighter;
                    line-height: 5rem;                    
                }
                span{
                    width: 75%;
                    display: block;
                    font-size: 1.4rem;
                    border-top: 1px $color-dark-grey solid;
                    margin-top: 10px;
                    padding-top: 10px;
                }
                a{
                    display: inline-block;
                    font-size: 1.4rem;                    
                    position: absolute;
                    bottom: 40px;
                }
                &:hover{
                    background-color: transparent;
                    h5{
                        color: $primary-color-yellow;
                    }
                    span{
                        color: $color-white;
                        border-color: $color-white;
                    }
                    a{
                        background-color: $primary-color-yellow;
                        border-color: $primary-color-yellow;
                    }
                }
            }
            &.swiper-slide-active{
                .recent_content{                    
                    background-color: transparent;
                    h5{
                        color: $primary-color-yellow;
                    }
                    span{
                        color: $color-white;
                        border-color: $color-white;
                    }
                    a{
                        background-color: $primary-color-yellow;
                        border-color: $primary-color-yellow;
                    }                    
                }
            }
        }
    }
}