.buy_sell_container{
    display: flex;
    align-items: center;
    position: relative;
    &::after{
        content: "";
        width: 1px;
        height: 100%;
        background-color: $color-white;
        position: absolute;
        top: 0;
        left: 50%;
    }
    .buy_content{
        width: 50%;
        position: relative;        
        img{
            width: 100%;
        }

        .bug_sell_content{
            position: absolute;
            width: 100%;
            height: 100%;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            top: 0;
            h4{
                font-size: 4rem;
                color: $color-white;
                font-weight: lighter;
            }
            a{
                margin-top: 15px;
            }
        }
    }    
}