.news_container{
    margin-bottom: 70px;
    .container_inner{
        .news_tabs{
            border-bottom: 2px $primary-color-yellow solid;
            ul{
                display: flex;
                li{
                    margin-right: 2px;
                    &:last-child{
                        margin-right: 0;
                    }
                    a{
                        display: block;
                        font-size: 1.6rem;
                        background-color: $color-light-grey;
                        padding: 10px 20px;
                        border-top-left-radius: 5px;
                        border-top-right-radius: 5px;
                        &.active{
                            background-color: $primary-color-yellow;
                        }
                    }
                }
            }
        }

        .news_tab_content{        
            .news_content{
                margin-top: 30px;
                display: flex;
                justify-content: space-between;            
                .left_sec{
                    width: 65%;
                    .big_news{
                        display: flex;
                        justify-content: space-between;
                        .big_news_img{
                            width: 48%;
                            img{
                                width: 100%;
                            }
                        }
                        .big_news_content{
                            width: 48%;
                            .tag{
                                display: inline-block;
                                background-color: $primary-color;
                                color: $color-white;
                                font-size: 1.2rem;
                                text-transform: uppercase;
                                padding: 5px 30px;
                                border-radius: 50px;
                                margin-top: 20px;
                            }                        
                            h4{
                                font-size: 2rem;
                                color: $primary-color-yellow;
                                font-weight: lighter;
                                margin-top: 10px;
                                line-height: 25px;
                            }
                            p{
                                font-size: 1.4rem;
                                line-height: 25px;
                                margin-top: 20px;
                            }
                            a{
                                margin-top: 30px;
                            }
                        }
                    }
                }

                .right_sec{
                    width: 30%;
                    ul{
                        position: relative;
                        height: 100%;
                        display: flex;
                        justify-content: space-between;
                        flex-flow: column;
                        li{                  
                            margin-bottom: 10px;      
                            a{
                                display: flex;
                                align-items: center;
                                justify-content: space-between;                            
                                img{
                                    max-width: 120px;
                                    margin-right: 20px;
                                }
                                h5{
                                    font-size: 1.8rem;
                                    color: $primary-color-yellow;
                                    font-weight: lighter;
                                    margin-top: 10px;
                                    transition: 0.3s;
                                }
                                .date{
                                    margin-top: 0;
                                }
                                &:hover{
                                    h5{
                                        color: $primary-color;
                                    }
                                }
                            }
                        }
                    }
                }

                .date{
                    display: block;
                    font-size: 1.4rem;
                    margin-top: 20px;
                    font-weight: bold;
                    font-family: $font-default;
                }                
            }
            .news_btn{
                width: 100%;
                text-align: center;
                margin-top: 50px;
            }
        }
    }
}