@font-face {
    font-family: 'PrataRegular';
    src: url('../../fonts/prata-regular.ttf');
    src: url('../../fonts/prata-regular.woff2') format('woff2'),
        url('../../fonts/prata-regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'LoraRegular';
    src: url('../../fonts/lora-variablefont_wght.ttf');
    src: url('../../fonts/lora-variablefont_wght.woff2') format('woff2'),
        url('../../fonts/lora-variablefont_wght.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'LoraItalic';
    src: url('../../fonts/lora-italic-variablefont_wght.ttf');
    src: url('../../fonts/lora-italic-variablefont_wght.woff2') format('woff2'),
        url('../../fonts/lora-italic-variablefont_wght.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

* {
    margin: 0;
    padding: 0;
    list-style: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    border: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
}

html {
    width: 100%;
    height: 100%;
    -webkit-text-size-adjust: 100%;
    font-size: 62.5%;
    width: 100%;
    overflow-x: hidden;
}

body {
    font-family: $font-default;
    color: $primary-color;
    height: 100%;
    font-size: 1rem;
}

a, a:hover {
    text-decoration: none;
    outline: none;
    cursor: pointer;
    color: $primary-color;
    font-family: $font-regular;
}

span{
    font-family: $font-regular;
}

img {
    border: none;
}

p, input, textarea {
    font-size: 1.6rem;
    font-family: $font-default;
    outline: none;
    color: $primary-color;
    margin-top: 0;
    margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6{
    margin-top: 0;
    margin-bottom: 0;
}

ul{
    list-style: none;
    margin: 0;
}

table,
table tr td,
table tr th {
    border: none
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

input[type=submit], input[type=button] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;    
    cursor: pointer;   
}

input::-ms-clear,textarea::-ms-clear {
    display: none;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}

::-moz-selection {
    color: $color-white;
    background: $primary-color-yellow;
}

::selection {
    color: $color-white;
    background: $primary-color-yellow;
}

.mt_70{
    margin-top: 70px;
}

.global_pl{
    padding-left: 30px;
}

.container{
    width: 95%;
    margin: auto;
    max-width: 1920px;
}

.container_inner{
    width: 85%;
    margin: auto;
    max-width: 1920px;
}

.btn{    
    font-size: 1.4rem;
    font-weight: bold;
    padding: 10px 30px;
    border-radius: 8px;
    display: inline-block;
    text-transform: uppercase;
    transition: 0.3s;
    &:hover{
        background-color: $primary-color;
        color: $color-white;
    }
}

.btn-yellow{
    background-color: $primary-color-yellow;
}

.btn-grey{
    background-color: $color-grey;
}

.btn-transparent{
    background-color: transparent;
    border: 1px $primary-color solid;
    &:hover{
        background-color: $primary-color-yellow;
        border-color: $primary-color-yellow;
        color: $primary-color;
    }
}

.overlay{
    position: fixed;
    top:0;
    left: 0;
    width:100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1000;
    display: none;
}

.overflow-hide{
    overflow: hidden;
}

.tab-content{
    display: none;
    &.active{
        display: block;
    }
}

/*-----Custom Radio Buttons-----*/

.custom_radio_cont{
    display: flex;
    .custom_radio {
        display: block;
        position: relative;
        padding-left: 25px; 
        padding-top: 4px;   
        cursor: pointer;
        font-size: 1.4rem;
        font-family: $font-italic;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        margin-right: 40px;
        &:last-child{
            margin-right: 0;
        }
        input{
            position: absolute;
            opacity: 0;
            cursor: pointer;
        }

        .checkmark {
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
            background-color: transparent;
            border: 1px $primary-color solid;
            border-radius: 50%;
            &:after {
                content: "";
                position: absolute;
                display: none;
                top: 7px;
                left: 7px;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background: $color-white;
            }
        }

        &:hover input ~ .checkmark {
            background-color: transparent;
            border: 1px $primary-color solid;
        }
          
        input:checked ~ .checkmark {
            background-color: $primary-color;
            border: none;
        }

        input:checked ~ .checkmark:after {
            display: block;
        }
    }
}

/*-----Custom Radio Buttons-----*/

/*-----Custom Select Buttons-----*/

.custom_select{
    background-color: transparent;
    border-bottom: 1px $primary-color solid;
    font-size: 1.4rem;
    font-family: $font-italic;
    padding-bottom: 5px;
    padding-right: 20px;
    padding-left: 5px;
    appearance: none;
    background: url("../../images/downarrow.svg") no-repeat right;
    background-size: 10px;
    cursor: pointer;
    background-position-y: 2px;
    width: 100%;
    &:focus-visible{
        outline: none;
    }
}

/*-----Custom Select Buttons-----*/

/*-----Custom Input Type-----*/

.custom_input{
    input[type=text]{
        font-size: 1.4rem;
        color: $primary-color;
        font-family: $font-italic;
        border-bottom: 1px $primary-color solid;
        padding-bottom: 10px;
        padding-left: 10px;
        background-color: transparent;
        width: 100%;
        &::placeholder{
            color: $primary-color;
        }
        &:focus{
            &::placeholder{
                color: transparent;
            }
        }
    }
}

/*-----Custom Input Type-----*/

/*-----Custom Checkbox-----*/


.custom_check_cont {
    .custom_checkbox{    
        display: block;
        position: relative;
        padding-left: 35px;    
        cursor: pointer;
        font-size: 1.4rem;
        padding-top: 5px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;    
        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
        }
        .checkmark {
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
            border-radius: 5px;
            background-color: #eee;
            &::after{
                content: "";
                position: absolute;
                display: none;
                left: 7px;
                top: 3px;
                width: 5px;
                height: 8px;
                border: solid white;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(35deg);
                -ms-transform: rotate(35deg);
                transform: rotate(35deg);
            }
        }
        &:hover input ~ .checkmark {
            background-color: $color-grey;
        }
        input:checked ~ .checkmark {
            background-color: $primary-color;
        }
        input:checked ~ .checkmark:after {
            display: block;
        }
    }
}  

  /*-----Custom Checkbox-----*/

  /*-----Custom File Upload-----*/

  .fileUpload {
	position: relative;
    span{
        display: inline-block;
        font-size: 1.4rem;
        font-weight: bold;
        padding: 10px 20px;
        background: $color-grey;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        color: $primary-color;
        text-transform: uppercase;
        transition: 0.3s;
        cursor: pointer;
        &:hover, &:active, &:focus{
            background: $primary-color-yellow;
            cursor: pointer;
        }
    }
    
    input{
        position: fixed;
        right: 100%;
        bottom: 100%;
        &.upload{
            position: absolute;
            top: 0;
            right: 0;
            margin: 0;
            padding: 0;
            font-size: 20px;            
            opacity: 0;
            filter: alpha(opacity=0);
            width: 100%;
            height: 100%;
            cursor: pointer;
        }
    }    
}

/*-----Custom File Upload-----*/

.global-pagination{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: left;
    .swiper-pagination-bullet{
        opacity: 1;
        background-color: $color-dark-grey;
        width: 10px;
        height: 10px;
        cursor: pointer;
        transition: 0.3s;
        &.swiper-pagination-bullet-active{
            background-color: $primary-color-yellow;  
            width: 25px;
            border-radius: 10px;
        }
    }
}