.dashboard_container{
    .myDashboard{
        .headingMedium{
            margin-top: 30px;
        }
                
        .limitBoxs{
            display: flex;
            .bid_limit_box{
                background-color: $color-white;
                padding: 20px;
                margin-top: 15px;
                margin-right: 30px;
                border-radius: 5px;
                box-shadow: 0px 0px 20px #ccc;
                h5{
                    font-size: 1.4rem;
                    font-family: $font-italic;
                }
                ul{
                    display: flex;
                    align-items: center;
                    width: 100%;
                    margin-top: 8px;
                    li{
                        margin-bottom: 0;
                        h4{
                            font-size: 2rem;
                            margin-top: 10px;
                        }
                    }
                }
                &:last-child{
                    width: 35%;
                }                
            }
        }        

        .bidsListBoxes{
            margin-top:50px;
            display: flex;
            justify-content: space-between;
            .bidBox{
                width: 32%;
                .headingMedium{
                    margin-top: 0;
                }
                .headSec{
                    margin-bottom:15px;
                    .viewAll{
                        text-decoration: underline;
                        font-size: 1.4rem;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color-yellow;
                        }
                    }
                }
                .bidsList{
                    width: 100%;
                    li{
                        width: 100%;
                        box-shadow: 0px 1px 16px 0px #ccc;
                        background: $color-white;
                        display: flex;
                        justify-content: space-between;
                        padding:3px;
                        border-radius:4px;
                        margin-bottom:10px;
                        &:last-child{
                            margin-bottom: 0;
                        }
                        .leftSec{
                            display: flex;
                            align-items: center;
                            .imgSec{
                                width:70px;
                                img{
                                    width: 100%;
                                }
                            }
                            .itemDetail{
                                margin-left: 10px;
                                h5{
                                    font-size: 1.2rem;
                                    font-family: $font-italic;
                                    color: $color-orange;
                                    font-weight: normal;
                                }
                                .price{
                                    margin-top: 10px;
                                    font-size: 1.6rem;
                                    font-weight: bold;
                                }
                            }
                        }
                        .rightSec{
                            padding-right: 10px;
                            display: flex;
                            flex-wrap: wrap;
                            align-items: center;
                            width:100px;
                            justify-content: flex-end;
                            span{
                                background: $color-grey;
                                font-size: 1rem;
                                display: inline-block;
                                text-transform: uppercase;
                                padding: 3px 10px;
                                border-radius: 5px;
                                font-family: $font-regular;
                            }
                            .leading{
                                background: $color-green;
                                color: $color-white;
                                font-size: 1rem;
                                display: block;
                                padding:3px 10px;
                                border-radius:4px;
                            }
                            &.shareIconSec{
                                align-items:baseline;
                                width:30px;
                                margin-top: 3px;
                                img{
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
                
                &.calenderSec{
                    width: 30%;
                    margin-top: 34px;
                    box-shadow: 0px 1px 16px 0px #0000001a;
                    background: $color-white;
                    border-radius: 4px;
                    .box{
                        padding: 15px;
                        .headingMedium{
                            padding-left: 5px;
                        }
                        .calenderSec{
                            margin-top: 15px;
                            text-align: center;
                            img{
                                width: 80%;                                
                            }
                        }
                        .eventSec{
                            margin-top: 20px;
                            max-height: 120px;
                            padding-right: 30px;
                            ul{
                                display: block;
                                width: 100%;
                                li{
                                    margin-bottom: 25px;
                                    h6{
                                        font-size: 1.2rem;
                                        font-family: "LoraItalic";
                                        font-weight: normal;
                                    }
                                    .eventDate{
                                        font-size: 1.3rem;
                                        font-weight: normal;
                                        font-weight: bold;
                                        margin-top:10px;
                                    }
                                    &:last-child{
                                        margin-right:0;
                                    }
                                }
                            }
                        }
                    }
                }
                &:last-child{
                    margin-right:0;
                }
            }
        }
        .bidsCharts{
            margin-top: 0;
            .bidsRatio{
                margin-top: 34px;
                box-shadow: 0px 1px 16px 0px #ccc;
                background: $color-white;
                border-radius: 4px;
                padding:18px 15px;
                .ratioSec{
                    text-align: center;
                    img{
                        width: 100%;
                    }
                }
            }
        }
        .notifySec{
            padding:0 30px 0 5px;
            margin-top: 20px;
            max-height: 350px;
            ul{
                li{
                    margin-bottom: 30px !important;
                    .content{
                        font-size: 1.4rem;
                        line-height: 22px;
                        margin-bottom: 10px;
                    }
                    span{
                        color: $color-orange;
                        margin-top: 5px;
                        display: block;
                        font-size: 1.2rem;
                    }

                    &:last-child{
                        margin-right:0;
                    }
                }
            }
        }

        .highlightInnerSec{
            margin-top: 50px;
            overflow: hidden;
            .auction_nav{
                width:10%;
                margin-right:3%;
            }
            .highlight_slider_container{
                width: 101%;
            }
            .highlight_sliderInner{
                .swiper-wrapper{
                    .item{
                        padding:0 10px;
                        border-right: 2px solid #e5e5e5;
                        &:hover{
                            box-shadow:none
                        }
                    }
                }
            }
        }

        .boxsesSecfooter{
            margin-top: 50px;
            display: flex;
            justify-content: space-between;
            .headingMedium{
                margin-top:5px;
            }
            .leftSec{
                width:48%;
                .contentExecutiveSec{
                    box-shadow: 0px 1px 16px 0px #ccc;
                    background: $color-white;
                    border-radius: 4px;
                    padding: 18px 22px;
                    .aboutDetail{
                        display: flex;
                        align-items: center;
                        margin-top: 20px;
                        .imgSec{
                            width:130px;
                            border-radius:50%;
                            overflow: hidden;
                            img{
                                width: 100%;
                            }
                        }
                        .about{
                            margin-left: 30px;
                            h3{
                                color: $color-orange;
                                font-size: 1.8rem;
                                font-weight: normal;
                            }
                            h5{
                                color:$primary-color;
                                font-size:1.2rem;
                                font-weight: normal;
                                margin-top:8px;
                            }
                            ul{
                                margin-top: 20px;
                                width: 100%;
                                display: block;
                                li{
                                    margin-bottom: 10px;
                                    a{
                                        display: flex;
                                        align-items: center;
                                        font-size:1.2rem;
                                        color: $primary-color;
                                        .icon{
                                            display: inline-block;
                                            background:url(../images/like.svg) no-repeat 0 0;
                                            width:30px;
                                            height:30px;
                                            background-size:contain;
                                            margin-right:10px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .downloadApp{
                    margin-top:5%;
                    box-shadow: 0px 1px 16px 0px #ccc;
                    background: $color-white;
                    border-radius: 4px;
                    padding: 18px 22px;
                    img{
                        width:100%;
                    }
                    ul{
                        margin-top: 20px;
                        display: flex;
                        justify-content: flex-start;
                        width: 100%;
                        li{
                            margin-bottom: 0;
                            width: 35%;
                            a{
                                img{
                                    width: 100%;
                                }
                            }
                            &:last-child{
                                margin-left: 30px;                                
                            }
                        }
                    }
                }
            }
            .rightSec{
                box-shadow: 0px 1px 16px 0px #ccc;
                background: $color-white;
                border-radius: 4px;
                padding: 18px 22px;
                width:48%;
                position: relative;
                .daysToGo{
                    position: absolute;
                    right:20px;
                    top:20px;
                    text-align: center;
                    span{
                        font-size: 1.6rem;
                        color: $primary-color;
                        font-weight: bold;
                        background:$color-pink;
                        padding: 5px;
                        border-radius: 50%;
                        display: block;
                        width:35px;
                        height:35px;
                        line-height:25px;
                        text-align: center;
                        margin: 0 auto;
                    }
                    p{
                        font-size:1rem;
                        color: $primary-color;
                        margin-top: 5px;
                    }
                }
                .aboutDetail{
                    display: flex;
                    margin-top: 20px;
                    .imgSec{
                        width: 100px;
                        img{
                            width: 100%;
                        }
                    }
                    .about{
                        margin:10px 0 0 20px;
                        h3{
                            font-size:1.6rem;
                            color: $primary-color;
                        }
                        h5{
                            font-size:1.2rem;
                            color: $primary-color;
                            font-weight: normal;
                            margin-top: 8px;
                        }
                        .onderNo{
                            margin-top:30px;
                            h3{
                                font-size:1.6rem;
                                color: $primary-color;
                                font-weight: normal;
                            }
                            h5{
                                font-size:1.2rem;
                                color: $primary-color;
                                font-weight: normal;
                                margin-top:5px;
                            }
                        }
                    }
                }
            }
            
            .trackingSteps{
                position: relative;
                margin-top:20px;
                padding-top:50px;
                .trackIcon{
                    width: 80px;
                    position: absolute;
                    top:0;
                    img{
                        width:100%;
                    }
                }
                .progressBar{
                    width: 100%;
                    background: $color-grey;
                    height: 8px;
                    border-radius: 10px;
                    position: relative;
                    .progress{
                        position: absolute;
                        height:8px;
                        border-radius:10px 0 0 10px;
                        background:$color-orange;
                        &:after{
                            content:"";
                            position: absolute;
                            right:0;
                            top:50%;
                            height:18px;
                            width:2px;
                            background:$color-orange;
                            transform: translate(0, -50%);
                        }
                    }
                }
                .list{
                    display: flex;
                    padding-top: 20px;
                    justify-content: space-between;
                    align-items: center;
                    li{
                        color: $primary-color;
                        align-items: center;
                        text-align: center;
                        font-size: 1.2rem;
                        line-height: 18px;
                        margin-bottom: 0;
                        &:first-child{
                            text-align: left;
                        }
                        &:last-child{
                            text-align: right;
                        }
                    }
                }
            }
            .note{
                color: $primary-color;
                font-weight: bold;
                font-size:0.9rem;
                font-family:$font-italic;
                margin-top:20px;
            }
        }
    }
}

.headingMedium{
    font-size: 1.6rem;
}
.display-flex{
    display: flex;
}
.justifySpaceBetween{
    justify-content: space-between;
}
.alignItemCenter{
    align-items: center;
}