.who_banner{
    margin-top: 30px;
    img{
        width: 100%;
        margin-bottom: 30px;
    }
    p{
        font-size: 1.4rem;
        margin-top: 20px;
        line-height: 30px;
    }
}

.culture_container{
    .culture_content{
        margin-top: 50px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .left_sec{
            width: 50%;
            overflow: hidden;
            .swiper-wrapper{
                width: 100%;
                img{
                    width: 100%;
                }
            }
            .global_heading{                
                margin-top: 20px;                
                .auction_nav{
                    width: 15%;
                    margin: 0 auto;
                }               
            }            
        }
        .right_sec{
            width: 48%;            
        }
    }

    p{
        font-size: 1.4rem;
        line-height: 25px;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
    }

    .culture_text{
        width: 100%;
        margin-top: 50px;
    }
}

.values_container{
    background-color: $color-light-grey;    
    .values_cont{
        display: flex;
        justify-content: space-between;
        .left_sec{
            width: 45%;
            overflow: hidden;
            padding-top: 50px;
            .value_content{                
                margin-top: 50px;
                position: relative;
                p{
                    font-size: 1.4rem;
                    line-height: 30px;
                }
                .global-pagination {
                    bottom: -70px;
                }
            }
        }
        .right_sec{
            width: 50%;
            overflow: hidden;
            img{
                width: 100%;
            }
        }
    }
}

.management_container{    
    .management_content{
        margin-top: 30px;
        p{
            font-size: 1.4rem;
            margin-top: 20px;
            line-height: 30px;
        }
    }

    .management_team{
        margin-top: 50px;
        ul{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            li{
                width: 30%;
                text-align: center;
                padding: 30px;
                padding-top: 110px;
                border-radius: 5px;
                margin-top: 150px;
                margin-bottom: 70px;
                box-shadow: 0px 20px 50px #ccc;
                position: relative;
                img{
                    width: 70%;
                    margin: auto;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: -50%;
                    border-radius: 50%;
                }
                h4{
                    font-size: 1.8rem;
                    color: $primary-color-yellow;
                    font-weight: lighter;                    
                    span{
                        display: block;
                        font-size: 1.4rem;
                        color: $primary-color;
                        margin-top: 10px;
                    }
                }

                a{
                    display: inline-block;
                    margin-top: 20px;                    
                    .icon_cont{
                        background-color: $color-light-grey;
                        padding: 10px;
                        border-radius: 50%;
                        width: 40px;
                        height: 40px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin: auto;
                        margin-bottom: 10px;
                        transition: 0.3s;
                        svg{
                            width: 25px;
                            height: 25px;                                                     
                        }
                    }                    
                    span{
                        display: block;
                        font-size: 1.4rem;
                        transition: 0.3s;
                    }
                    &:hover{
                        .icon_cont{
                            background-color: $primary-color-yellow;
                        }
                        span{
                            color: $primary-color-yellow;
                        }                        
                    }
                }
            }
        }
    }
}