.global_heading {
  display: flex;
  justify-content: space-between;
  align-items: center;

  h2{
    display: inline-block;
    margin-bottom: 0;
    color: $primary-color-yellow;
    font-size: 4rem;
    font-weight: normal;
    position: relative;
    &::before{
      content: "";
      width: 150%;
      height: 1px;
      background-color: $primary-color-yellow;
      position: absolute;
      left: -180px;
      bottom: 7px;
    }
    &::after{
      content: "";
      width: 100%;
      height: 1px;
      background-color: $primary-color-yellow;
      position: absolute;
      left: 0;
      bottom: 7px;
    }
  }

  .auction_nav{
    display: flex;
    justify-content: space-between;
    align-items: center;    
    width: 8%;
    margin-right: 8%;
    .slider_nav{            
      position: static;
      margin-top: 0;
      background-color: $primary-color-yellow;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      padding: 18px;      
      &::after{
        font-size: 12px;
        font-weight: bold;
        color: $primary-color;
      }        
    }
  }

  .breadcrumb{
    ul{
      display: flex;
      align-items: center;
      li{
        font-size: 1.4rem;
        a{
          display: block;
          transition: 0.3s;
          &:link{
            font-weight: bold;
            &::before{
              content: "/";
              font-size: 1.4rem;
            }
            &:hover{
              color: $primary-color-yellow;
              &::before{
                color: $primary-color;
              }
            }
          }          
        }

        &:first-child{
          margin-left: 0;        
        }
      }
    }
  }
}

.heading_inner{
  display: flex;
  align-items: center;
  justify-content: space-between;
  .global_heading{
    width: 8%;
    .auction_nav{
      width: 100%;
      margin-right: 0;
    }
  }
  h3{
    font-size: 2rem;
    color: $primary-color-yellow;
    font-weight: lighter;
  }
}