.login_reg_sec{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2000;    
    display: none;
    
    .login_form_sec{
        width: 400px;        
        padding: 50px;
        background: $color-white;
    }

    .reg_form_sec{
        display: none;
        width: 900px;
        padding: 50px;
        background: $color-white;
        form{
            ul{
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                li{
                    margin: 30px 18px;
                    width: 28%;
                }
            }
        }
    }

    .closeBtn{
        position: absolute;
        right:-15px;
        top:-15px;
        background-color: $primary-color-yellow;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
        transition: 0.3s;
        box-shadow: -3px 3px 5px 0px rgba(0,0,0,0.5);               
        &:hover{
            background-color: $primary-color;
            color: $color-white;
        }
    }

    h3{
        font-size: 3rem;
        color: $primary-color-yellow;
        text-align: center;
        font-weight: lighter;
    }

    form{
        margin-top: 30px;
        ul{
            li{
                margin-bottom: 30px;
                border-bottom: 1px $primary-color solid;                
                input[type=text], input[type=password]{
                    font-size: 1.6rem;
                    font-family: $font-italic;
                    padding: 10px 15px;                    
                    width: 100%;
                    &:focus{
                        &::placeholder{
                            color: transparent;
                        }
                    }
                }
                &:last-child{
                    text-align: center;
                    border-bottom: none;
                    input[type=submit]{
                        padding: 15px 50px 10px;
                        font-weight: normal;
                    }
                }
                &.password_sec{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    a{
                        display: inline-block;
                        width: 30px;
                        img{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }

    .create_acc{
        text-align: center;
        a{
            display: inline-block;
            font-size: 1.6rem;
            text-decoration: underline;
            transition: 0.3s;
            &:hover{
                color: $primary-color-yellow;
            }
        }
    }
}