@media only screen and (max-width: 991px){
    .pos_relative{
        position: relative;
    }

    .mt_70{
        margin-top: 50px;
    }

    header{
        .sub_header{
            display: none;
            background-color: $color-white;
            .container{
                width: 100%;
                ul{
                    width: 100%;                    
                    li{
                        a{
                            &.subnav_link{
                                &::after{
                                    content: "";
                                    background: url("../../images/downarrow.svg") no-repeat center right;
                                    background-size: 100%;
                                    width: 15px;
                                    height: 15px;
                                    position: absolute;
                                    right: 0;
                                }
                            } 
                        }
                        .sub_nav{
                            ul{
                                position: static;
                                box-shadow: none;
                                background-color: $color-white;
                                li{
                                    padding: 0;
                                    border-bottom: 1px #272727 solid;
                                    a{
                                        text-align: left;
                                        border-bottom: none;
                                    }
                                    &:last-child{
                                        border-bottom: none;
                                    }
                                }                                
                            }
                        }                        
                    }
                }
            }            
        }
        .main_header{
            .container{
                .nav{
                    display: none;
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    background-color: #fff;
                    width: 100%;
                    height: 100%;
                    z-index: 1000;
                    padding: 30px;
                    padding-bottom: 70px;
                    overflow-y: auto;
                    ul{
                        display: block;
                        li{
                            border-bottom: 1px $primary-color solid;
                            margin-bottom: 10px;
                            a{ 
                                padding: 10px 0;
                                position: relative;
                                &.subnav_link{
                                    &::after{
                                        content: "";
                                        background: url("../../images/downarrow.svg") no-repeat center right;
                                        background-size: 100%;
                                        width: 15px;
                                        height: 15px;
                                        position: absolute;
                                        right: 0;
                                    }
                                }                                
                                span{
                                    display: none;
                                }
                            }
                            .sub_nav_container{
                                display: none;                                
                                margin: 0;
                                padding: 0; 
                                width: 100%;
                                position: static;
                                transition: none;
                                ul{
                                    margin-left: 0;
                                    li{
                                        border-bottom: none;
                                        a{
                                            text-align: left;
                                            &:after{
                                                display: none;
                                            }
                                        }                                        
                                    }
                                    &::before{
                                        display: none;
                                    }
                                }
                                img{
                                    display: none;
                                }
                            }
                        }
                    }
                }
                .logo{
                    width: 35%;
                }

                .login_sec{
                    width: 50%;
                    .login_btn{
                        display: none;
                    }
                    .login_icon_btn{
                        display: block;
                        width: 30px;
                    }
                }

                .hamburger{
                    display: block;
                    width: 35px;
                    margin-left: 15px;
                }
            }
        }
    }

    .global_pl{
        padding-left: 30px;
    }

    .global_heading{
        h2{
            font-size: 2.5rem;
            &::before, &::after{
                bottom: 4px;
            }            
        }
        .auction_nav{
            display: none;
            margin-right: 0;
        }
    }

    .auction_container{
        padding-top: 50px;
        padding-left: 0;
        width: 90%;
        margin: auto;
        .auction_way{
            display: block;
            .auction_content, .auction_content p, .auction_slider_container{
                width: 100%;
            }
            .auction_slider_container{
                margin-top: 30px;
                padding-bottom: 50px;
            }
        }
    }

    .highlight_container{
        width: 90%;
        margin: auto;
        margin-top: 70px;
        .highlight_slider_container{
            margin-top: 0;
        }
    }

    .recent_auction_container{
        width: 90%;
        margin: auto;
        margin-top: 70px;
        .recent_slider_container{
            width: 90%;
            margin: auto;
            margin-top: 30px;
            .swiper-wrapper{
                .item{
                    .recent_content{
                        text-align: center;
                        padding: 15px;
                        top: auto;
                        bottom: 0;
                        height: 45%;
                        border-top-left-radius: 0;
                        border-top-right-radius: 0;       
                        background-color: rgba(0,0,0,0.8);
                        h5{
                            font-size: 1.8rem;
                            line-height: normal;
                            color: $color-white;
                        }
                        span{
                            width: 100%;
                            color: $color-white;
                        }

                        a{
                            position: static;
                            margin-top: 15px;
                            border-color: $color-white;
                            color: $color-white;
                        }
                    }
                }
            }
        }
    }

    .price_container{ 
        .price_slider_container{
            .swiper-wrapper{
                .item{
                    h4{
                        font-size: 2.8rem;
                    }
                }
            }
        }
    }

    .buy_sell_container{
        display: block;
        &::after{
            display: none;
        }
        .buy_content{
            width: 100%;
            img{
                min-height: 150px;
                object-fit: cover;
            }
            .bug_sell_content{
                h4{
                    font-size: 2.5rem;
                }
            }
        }
    }

    .ourCollector_container {
        .ourCollector_slider_container{
            padding-left: 30px;
            .swiper-wrapper{
                .item{
                    border-color: $primary-color;
                    .collectorProfile{
                        opacity: 1;
                    }
                }                
            }
        }
    }    

    .dashboard_container{
        .dashboard_links{
            width: 30%;
            .profile_links{
                ul{
                    li{
                        a{
                            font-size: 1.4rem;
                            padding-left: 20px;
                        }
                    }
                }
            }
        }
        .dashboard_sec{
            padding: 30px 15px;
            ul{
                width: 100%;
                &.col-3{
                    li{
                        width: 48%;
                        &.user_gender{
                            label{
                                margin-right: 20px;
                            }
                        }
                    }
                }
            }
            .bid_limit_box{
                width: 100%
            }
        }
    }

    .culture_container{
        .culture_content{        
            display: block;    
            .left_sec, .right_sec{
                width: 100%;
            }   
            .right_sec, .culture_text{
                margin-top: 30px;
            }            
        }
    }

    .values_container{
        .values_cont{
            display: block;
            .left_sec, .right_sec{
                width: 100%;
                .value_content{
                    margin-top: 20px;
                    .global-pagination{
                        position: static;
                        margin-top: 15px;
                    }
                }
            }   
            .right_sec{
                margin-top: 30px;
            }
        }
    }
}

@media screen and (min-width: 641px) and (max-width: 1199px){
    body{            
        .global_heading{
            h2{
                &::before{
                    left: -70px;
                }
            }
            .auction_nav{
                display: flex !important;
                margin-right: 0;
                width: 15% !important;
            }
        }

        .artist_category_container{
            &::after{
                left: 47.5;
            }
            .artist_content{
                .artist_info{
                    display: block;
                    .artist_image{
                        width: 70%;
                    }
                    .artist_info_content{
                        width: 90%;
                    }
                }
            }
        }

        .subscribe_container{
            .subscribe_content{
                h4{
                    font-size: 2.5rem;
                }
                p, form{
                    width: 90%;
                }            
            }
        }
        
        .login_reg_sec{
            .reg_form_sec{
                width: 700px;
                padding: 30px;
                form{
                    ul{
                        li{
                            margin: 20px;
                            width: 26%;
                        }
                    }
                }
            }
        }                  
        
        .management_container{
            .management_team{
                ul{
                    li{
                        width: 45%;
                        padding-top: 65px;
                    }
                }
            }
        }

        .workenv_container{
            padding-bottom: 50px;
            &::after{
                content: none;                
            }
        }

        .heading_inner{
            .global_heading{
                width: 15%;
                .auction_nav{
                    width: 100% !important;
                }
            }
        }

        .employee_container{
            .employee_cont{
                .employee_slider{
                    .item{
                        .employeehead_cont{
                            .employee_name{
                                h4{
                                    font-size: 1.8rem;
                                    line-height: 22px;                                    
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 992px) and (max-width: 1199px){
    .auction_container {
        .auction_way{
            .auction_content{
                padding-bottom: 30px;
                p{
                    width: 85%;
                }
            }
        }
    }    
}

@media only screen and (max-width: 1199px){
    .highlight_slider_container{
        .swiper-wrapper{
            .item{
                .highlight_button{
                    a{
                        padding: 10px 24px;
                        font-size: 1.2rem;
                    }
                }
                .highlight_bid{
                    h4{
                        font-size: 1.8rem;
                    }
                }
            }
        }
    }    
}

@media only screen and (max-width: 640px){
    .global_heading {
        h2{
            &::before{
                left: -140px;
            }
        }

        .breadcrumb{
            ul{
                li{
                    a{
                        font-size: 1.2rem;
                    }
                }
            }
        }
    }
    .price_container{
        .price_tabs{
            ul{                
                justify-content: flex-start;                
                li{                    
                    margin: 0 15px;
                    a{
                        font-size: 1.4rem;
                        text-align: center;                        
                    }
                }
            }
        }
    }

    .artist_category_container{        
        display: block;
        &::after{
            width: 90%;
            height: 1px;
            left: 0;
            right: 0;
            margin: auto;
            top: 47%;
        }
        .artist_content{
            width: 90%;
            margin: auto;
            .artist_info{
                display: block;
                .artist_image, .artist_info_content{
                    width: 100%;
                }
                .artist_info_content{
                    a{
                        margin-top: 20px;
                    }
                }
            }
        }

        .category_content{
            margin-top: 70px;
            .global_heading{
                h2{
                    &::before{
                        left: -100px;
                        right: auto;
                    }
                }
            }
        }
    }    

    .subscribe_container {
        padding: 50px 15px;
        .subscribe_content{
            width: 90%;
            margin: auto;
            h4{
                font-size: 2.5rem;
            }
            p{
                width: 100%;
            }
            form{                
                width: 100%;
                ul{
                    display: block;
                    li{
                        width: 100%;
                        margin: 0;
                        margin-bottom: 30px;    
                        &:last-child{
                            text-align: center;
                        }                    
                    }
                }
            }
        }
    }

    footer{
        padding-top: 50px;
        .main_footer{
            display: block;
            width: 90%;
            padding: 0 15px;            
            .footer_list{
                width: 100%;
                margin-bottom: 30px;
                border-bottom: 1px $primary-color solid;
                &:last-child{
                    border-bottom: none;
                    h5{
                        &::after{
                            content: none;
                        }
                    }
                }
                h5{
                    padding-bottom: 15px;
                    font-size: 1.6rem;
                    position: relative;
                    &::after{
                        content: "prev";
                        font-family: swiper-icons;
                        font-size: 1.5rem;
                        transform: rotate(270deg);
                        position: absolute;
                        right: 5px;
                        color: $primary-color;
                        font-weight: bold;
                    }
                }
                ul{
                    display: none;
                    margin-top: 10px;
                    padding-bottom: 30px;
                }

                .app_links{
                    margin-top: 10px;
                    display: flex;
                    align-items: center;
                    a{
                        margin-bottom: 0;
                        img{
                            width: 80%;
                        }
                    }
                }

                .social_links{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    h5{
                        padding-bottom: 0;
                        margin-right: 50px;
                    }
                    a{
                        margin-top: 0;
                        &:last-child{
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .sub_footer{
            .container{
                display: block;
                .left_sec{
                    ul{
                        justify-content: center;
                        li{
                            margin-right: 10px;
                            a{
                                text-align: center;
                            }
                        }
                    }
                }
                .right_sec{
                    margin-top: 20px;
                    p{
                        text-align: center;
                    }
                }
            }
        }
    }

    body{    
        .login_reg_sec{
            width: 100%;
            .closeBtn{
                right: 0;
            }
            h3{
                font-size: 2.5rem;
            }
            form{
                margin-top: 20px;
            }
            .login_form_sec, .reg_form_sec{
                width: 90%;
                margin: auto;
                padding: 25px ;
            }
            .reg_form_sec{
                ul{
                    display: block;
                    li{
                        width: 100%;
                        margin: 0;
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }

    .dashboard_container{
        display: block;

        .dashboard_links{
            width: 100%;
            padding: 30px 0;
        }

        .dashboard_sec{
            width: 100%;
            padding: 30px 20px;
            ul{
                width: 100%;
                &.col-6, &.col-3{
                    li{
                        width: 100%;
                    }
                }
            }
            .user_info{
                ul{
                    display: block;
                    li{
                        width: 100%;
                        margin-bottom: 0px;
                        padding: 20px 0;
                        &.user_interested{
                            span{
                                position: static;
                                margin-top: 5px;
                            }
                        }                        
                    }                    
                }
            }
            .bid_limit_box{
                width: 100%;
                ul{
                    li{
                        margin-bottom: 20px;
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }
    }

    .who_banner{
        img{
            min-height: 350px;
            object-fit: cover;
            margin-bottom: 0;
        }

        .heading_inner{
            margin-top: 30px;
        }
    }

    .culture_container{
        .culture_content{
            .left_sec{
                .global_heading{
                    .auction_nav{
                        width: 30%;
                        display: flex;
                    }
                }
            }
        }
    }

    .management_container{
        .management_team{
            margin-top: 20px;
            ul{
                li{
                    width: 100%;
                    padding-top: 70px;
                    margin-bottom: 50px;
                }
            }
        }
    }

    .workenv_container{
        .container_inner{
            display: block;
            .left_sec, .right_sec{
                width: 100%;
            }
            .right_sec{
                margin-top: 30px;
            }
        }
    }

    .employee_container{
        .employee_cont{
            .employee_slider{
                .item{
                    padding: 15px;
                    .employeehead_cont{
                        display: block;
                        .employee_name{
                            margin-left: 0;
                            text-align: center;
                            h4{
                                margin-top: 10px;
                                font-size: 1.8rem;
                                line-height: 22px;                                    
                            }
                        }
                    }
                }
            }
        }
    }

}