.services_slider_container{
    background-color: $color-light-grey;
    padding: 70px 30px;
    margin-top: 30px;
    position: relative;
    .services_slider{        
        .swiper-wrapper{
            .item{
                background-color: $primary-color;
                border-radius: 10px;
                text-align: center;
                padding: 30px 0;
                padding-top: 0;
                transition: 0.3s;                
                .services_content{
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    span{
                        display: block;
                        color: $color-white;
                        font-size: 15rem;
                        opacity: 0.1;
                    }
                    h5{
                        color: $color-white;
                        font-size: 3.5rem;
                        font-weight: lighter;
                        position: absolute;                        
                        left: 0;
                        right: 0;
                        margin: auto;
                        width: 70%;
                    }
                }
                img{
                    width: 100%;
                    margin-left: 30px;
                    max-height: 200px;
                    object-fit: cover;
                    border-radius: 5px;
                }
                &:hover, &.swiper-slide-active{
                    transform: scale(1.1);
                    .services_content{
                        h5{
                            color: $primary-color-yellow;
                        }
                    }                    
                }
            }
        }
    }

    .services_btn{
        position: absolute;
        bottom: -18px;
        left: 0;
        right: 0;
        margin: auto;
        text-align: center;        
    }
}