.highlight_slider_container{
    margin-top: 30px;

    .swiper-wrapper{
        .item{
            text-align: center;
            padding: 30px;
            transition: 0.3s;
            &:hover, &.swiper-slide-active{
                box-shadow: 0px 20px 50px #ccc;
            }

            .highlight_tools{
                display: flex;
                align-items: center;
                justify-content: space-between;
                a{
                    display: inline-block;
                    width: 30px;
                    img{
                        width: 100%;
                        height: 100%;
                        margin-top: 0;
                    }
                }
                .highlight_3d{
                    a{                    
                        display: flex;
                        align-items: center;
                        span{
                            font-size: 1.2rem;
                            font-weight: bold;
                            padding-left: 5px;
                            text-decoration: underline;
                        }
                    }
                }

                .highlight_like{
                    a{
                        &:first-child{
                            margin-right: 10px;
                        }
                    }
                }
            }

            img{
                max-width: 100%;
                height: 250px;
                object-fit: cover;
                margin-top: 25px;
            }

            .highlight_detail{
                display: flex;
                align-items: center;
                justify-content: space-between;

                .highlight_name{
                    margin-top: 20px;
                    h4{
                        font-size: 2rem;
                        color: $primary-color-yellow;
                        font-weight: normal;
                        margin-top: 0;
                        margin-bottom: 0;
                    }
                    span{
                        display: block;
                        color: $primary-color;
                        font-size: 1.4rem;
                        margin-top: 10px;
                    }
                }
                .highlight_lot{
                    span{
                        background-color: $color-grey;
                        font-size: 1.2rem;
                        display: inline-block;
                        padding: 5px 20px;
                        border-radius: 15px;
                        line-height: normal;
                        text-transform: uppercase;                        
                    }
                }

            }

            .highlight_bid{
                display: flex;
                margin-top: 20px;                
                span{
                    display: block;
                    text-align: left;
                    font-size: 1.4rem;
                }
                h4{
                    font-size: 2rem;
                    margin-top: 10px;
                }

                .current_bid{
                    margin-right: 50px;
                }
            }

            .highlight_button{
                margin-top: 20px;
                display: flex;
                align-items: center;
                a{
                    display: inline-block;
                    font-size: 1.4rem;
                    text-transform: uppercase;   
                    padding: 10px 30px;
                    border-radius: 10px;
                    font-weight: bold;                    
                }
                div{
                    &:first-child{
                        margin-right: 30px;
                    }
                }
            }

            .highlight_time{
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-radius: 10px;
                margin-top: 20px;
                background-color: $color-pink;
                padding: 10px 15px;
                .bid_time{
                    p{
                        font-size: 1.2rem;                        
                    }
                }
                .bid_clock{
                    p{
                        font-weight: bold;
                    }
                }
            }
        }
    }
}