header{
  .sub_header{
    background-color: $color-light-grey;
    ul{
      display: flex;
      justify-content: flex-end;
      li{
        position: relative;
        a{
          font-size: 1.3rem;
          display: block;
          padding: 10px 20px;
          transition: 0.3s;
          &:hover{
            color: $primary-color-yellow;
            .sub_nav{
              display: block;
            }
          }
        }
        &:last-child{
          a{
            padding-right: 0;
          }
        }
        .sub_nav{      
          display: none;
          ul{            
            display: block;
            background-color: $color-light-grey;
            position: absolute;
            left: -50%;
            right: 0;
            width: 100%;
            min-width: 200px;
            top: 100%;
            z-index: 300;
            box-shadow: 0px 20px 50px #ccc;
            li{
              padding: 0 30px;
              a{
                text-align: center;
                border-bottom: 1px $primary-color solid;                
              }
              &:last-child{
                a{
                  padding-right: 0;
                  border-bottom: none;
                }
              }
            }
          }          
        }
        &:hover{
          .sub_nav{
            display: block;
          }
        }
      }      
    }    
  }

  .main_header{
    padding-top: 20px;    
    .container{          
      display: flex;
      align-items: center;
      justify-content: space-between;

      .logo{
        width: 15%;
        position: relative;
        z-index: 200;
        a{
          display: inline-block;
          img{
            width: 100%;
          }
        }
      }

      .nav{
        width: 45%;
        ul{
          display: flex;
          align-items: center;
          justify-content: space-between;
          li{
            position: relative;            
            a{
              display: block;
              font-size: 1.6rem;
              text-transform: uppercase;
              transition: 0.3s;
              &:hover{
                color: $primary-color-yellow;
              }        
            }
            .sub_nav_container{
              position: absolute;              
              left: 0;
              top: 100%;
              padding: 50px 1000em;
              margin: 0 -1000em;
              z-index: 100;
              background-color: $color-white;
              display: flex;
              visibility: hidden;
              opacity: 0;      
              transition: 0.5s;        
              ul{
                display: block;
                min-width: 150px;
                position: relative;
                margin-left: -60px;
                &::before{
                  content: "";
                  width: 0; 
                  height: 0; 
                  border-left: 6px solid transparent;
                  border-right: 6px solid transparent;                  
                  border-top: 6px solid $primary-color;
                  position: absolute;
                  right: 0;
                  top: -25px;
                }
                li{
                  a{
                    border-bottom: 1px $primary-color solid;
                    text-align: right;
                    padding: 10px 0;                    
                  }
                  &:last-child{
                    a{
                      border-bottom: none;
                    }                  
                  }
                }                
              }

              img{
                margin-left: 50px;
              }
            }
            &:hover{
              .sub_nav_container{              
                visibility: visible;
                opacity: 1;                
              }
            }
          }
        }
      }

      .login_sec{        
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        z-index: 200;

        .login_icon_btn{
          display: none;
          width: 38px;
          img{
            width: 100%;
          }
        }
        
        .search_btn{
          display: inline-flex;
          align-items: center;
          justify-content: center;
          padding: 12px 15px;
          border-radius: 8px;
          background-color: $color-light-grey;
          margin-left: 10px;
          img{
            width: 15px;
          }
        }

        .search_box{
          display: flex;
          position: absolute;          
          right: 0;
          background-color: $color-light-grey;
          border-radius: 8px;          
          align-items: center;
          justify-content: space-between;
          padding: 15px;
          opacity: 0;
          transition: 0.5s ease all;
          width: 0;
          z-index: -1;
          input[type=text]{
            display: block;
            width: 100%;
            background-color: transparent;            
            font-size: 1.6rem;
          }
          a{
            display: inline-block;
            width: 15px;
            img{
              width: 100%;
            }
          }
          &.show{
            width: auto;
            left: 0;
            z-index: 10;
            opacity: 1;
            overflow: visible;
          }
        }
      }      

      .hamburger{
        display: none;
        width: 45px;
        height: 30px;
        position: relative;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .5s ease-in-out;
        -moz-transition: .5s ease-in-out;
        -o-transition: .5s ease-in-out;
        transition: .5s ease-in-out;
        cursor: pointer;
        span{
          display: block;
          position: absolute;
          height: 4px;
          width: 100%;
          background: $primary-color;
          border-radius: 20px;
          opacity: 1;
          left: 0;
          -webkit-transform: rotate(0deg);
          -moz-transform: rotate(0deg);
          -o-transform: rotate(0deg);
          transform: rotate(0deg);
          -webkit-transition: .25s ease-in-out;
          -moz-transition: .25s ease-in-out;
          -o-transition: .25s ease-in-out;
          transition: .25s ease-in-out;
          -webkit-transform-origin: left center;
          -moz-transform-origin: left center;
          -o-transform-origin: left center;
          transform-origin: left center;
          &:nth-child(1){
            top: 0px;            
          }
          &:nth-child(2){
            top: 13px;            
          }
          &:nth-child(3){
            bottom: 0;
            width: 60%;
          }
        }
        &.open{
          span{
            &:nth-child(1){
              -webkit-transform: rotate(45deg);
              -moz-transform: rotate(45deg);
              -o-transform: rotate(45deg);
              transform: rotate(45deg);
              top: -3px;
              left: 8px;
            }
            &:nth-child(2) {
              width: 0%;
              opacity: 0;
            }            
            &:nth-child(3) {
              -webkit-transform: rotate(-45deg);
              -moz-transform: rotate(-45deg);
              -o-transform: rotate(-45deg);
              transform: rotate(-45deg);
              top: 22px;
              left: 8px;
              width: 100%;
            }
          }
        }
        &:hover{
          span{
            background: $primary-color-yellow;
          }
        }
      }
    }
  }
}