footer{
    padding-top: 70px;
    background-color: $color-light-grey;
    .container{
        width: 85%;
        margin: auto;
    }
    .main_footer{
        display: flex;
        justify-content: space-between;
        .footer_list{
            width: 25%;
            h5{
                font-size: 2rem;
                font-weight: lighter;
                color: $primary-color;
            }
            ul{
                margin-top: 30px;
                li{
                    margin-top: 20px;
                    &:first-child{
                        margin-top: 0;
                    }
                    a{
                        display: block;
                        font-size: 1.6rem;
                        color: $footer-grey;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color;
                        }
                    }
                }
            }
            .app_links{
                margin-top: 30px;
                a{
                    display: block; 
                    margin-bottom: 30px;
                    img{
                        width: 50%;
                    }                   
                }
            }

            .social_links{
                margin-top: 50px;
                a{
                    display: inline-block;
                    width: 40px;
                    margin-top: 15px;
                    margin-right: 10px;
                    img{
                        width: 100%;
                    }
                }
            }
        }
    }

    .sub_footer{
        background-color: $color-white;
        margin-top: 50px;
        padding: 15px 0;
        .container{
            display: flex;
            align-items: center;
            justify-content: space-between;
            .left_sec{
                ul{
                    display: flex;
                    align-items: center;
                    li{
                        margin-right: 20px;
                        a{
                            display: block;
                            font-size: 1.2rem;
                            color: $primary-color;
                            transition: 0.3s;
                            padding: 10px 0;
                            &:hover{
                                color: $primary-color-yellow;
                            }
                        }
                    }
                }
            }
            .right_sec{
                p{
                    font-size: 1.2rem;
                }
            }
        }
    }
}